      getBaseUrl() {
        const loading = this.$loading({
          lock: true,
          text: '加载中',
          spinner: 'el-icon-loading',
          background: '#fff'
        });
// http://172.17.130.94:8184/RongApi/Report/MobileReport?orderId=257183847478000
        axios.post('https://api.miduoduo.club/RongApi/Report/GetHeLiCallBackHost').then(res => {
          return res.data
        }).then(res => {
          //  axios.get(`http://172.17.130.94:8184/RongApi/Report/MobileReport?orderId=${this.orderId}`).then(res => {
           axios.get(`${res}/RongApi/Report/MobileReport?orderId=${this.orderId}`).then(res => {
              setTimeout(() => {
                loading.close();
              }, 500);
             if(res.data.mobile==null && res.data.multiloan==null &&res.data.phonelist==null ) {
                this.$alert('报告获取失败', {
                    title: '提示',
                    confirmButtonText: '确定',
                    callback: action => {
                       window.close()
                    }
                });
             }
             if(res.data.mobile) {
              var replacePre = res.data.mobile
              var parsePre = replacePre.replace( /\\\"/g,'')
              parsePre = parsePre.replace(/\\/g, '')
              var bbb = JSON.parse(parsePre)
             }
             if(res.data.multiloan) {
               var aaa = JSON.parse(res.data.multiloan.replace(/\\\"/g,''))
             }
             if(res.data.phonelist) {
               console.log(res.data.phonelist)
                try {
                  var ccc = JSON.parse(res.data.phonelist.replace(/\\\"/g,''))
                } catch (error) {
                  var ccc = null
                }
             }

              if(ccc) {
                this.phone_list = ccc.phone_list
                this.phone_list_total = ccc.phone_list.length || 0;
              } else {
                this.phone_list = []
              }
              if(bbb) {
                this.head_info = bbb.tianji_api_taojinyunreport_reportdetail_response.head_info
                this.input_info = bbb.tianji_api_taojinyunreport_reportdetail_response.input_info
                this.basic_info = bbb.tianji_api_taojinyunreport_reportdetail_response.basic_info
                this.risk_analysis = bbb.tianji_api_taojinyunreport_reportdetail_response.risk_analysis
                this.user_info_check = bbb.tianji_api_taojinyunreport_reportdetail_response.user_info_check
                this.user_portrait = bbb.tianji_api_taojinyunreport_reportdetail_response.user_portrait
                //特殊号码类别
                this.special_cate = bbb.tianji_api_taojinyunreport_reportdetail_response.special_cate
                this.special_cate_total = this.special_cate.length||0;
                //紧急联系人分析
                this.emergency_analysis = bbb.tianji_api_taojinyunreport_reportdetail_response.emergency_analysis
                this.emergency_analysis_total = bbb.tianji_api_taojinyunreport_reportdetail_response.emergency_analysis.length || 0
                //联系人地区分析
                this.contact_area_analysis = bbb.tianji_api_taojinyunreport_reportdetail_response.contact_area_analysis
                this.contact_area_analysis_total = bbb.tianji_api_taojinyunreport_reportdetail_response.contact_area_analysis.length || 0

                  //运营商月消费
                this.monthly_consumption = bbb.tianji_api_taojinyunreport_reportdetail_response.monthly_consumption
                this.monthly_consumption_total = bbb.tianji_api_taojinyunreport_reportdetail_response.monthly_consumption.length || 0

                  //通话地区分析
                this.tel_area_analysis = bbb.tianji_api_taojinyunreport_reportdetail_response.tel_area_analysis
                this.tel_area_analysis_total = bbb.tianji_api_taojinyunreport_reportdetail_response.tel_area_analysis.length || 0

                //通话记录
                this.call_log = bbb.tianji_api_taojinyunreport_reportdetail_response.call_log
                this.call_log_total = bbb.tianji_api_taojinyunreport_reportdetail_response.call_log.length || 0

                //出行分析
                this.trip_info = bbb.tianji_api_taojinyunreport_reportdetail_response.trip_info
                this.trip_info_total = bbb.tianji_api_taojinyunreport_reportdetail_response.trip_info.length || 0
              }

              if(aaa && aaa.tianji_api_tianjireport_multiloanreportv20_response) {
                //多头报告
                this.LongReport = aaa.tianji_api_tianjireport_multiloanreportv20_response.features
              }
           })
           axios.post(`${res}/RongApi/Report/GetUserInfo?orderId=${this.orderId}`).then(res => {
             console.log(res)
             this.preImg = res.data.idNoPreImg
             this.afterImg = res.data.idNoAfterImg
             this.realImg = res.data.idNoRealImg
             this.domainName = res.data.ImgPath || 'http://img.miduoduo.club'
           })
        })
      }
