<template>
  <div class="mainWrap">
    <ul>
      <li :class="{main: item.main}" v-for="item in list" :key="item.index" @click="goAnchor(item.key)">
        {{ item.describe }}
      </li>
    </ul>
  </div>
</template>

<script>
  export default {
    props: {
      list: {
        type: Array,
        default: []
      },
    },
    data() {
      return {
        timer: null,
        a: [{
          text: '基本信息',
          main: true,
          id: 'anchor-1'
        },
        {
          text: '用户输入信息',
          main: true,
          id: 'anchor-2'
        }]
      }
    },
    methods: {
      goAnchor(selector) {
        var anchor = document.querySelector('.' + selector)
        var begin = document.documentElement.scrollTop;
        var end = anchor.offsetTop

        clearInterval(this.timer);
         this.timer=setInterval(()=> {
						begin=begin+(end-begin)/8;
            window.scrollTo(0,begin);
            if(Math.round(begin)==end) {
              clearInterval(this.timer);
              this.timer = null
              console.log("??", Math.round(begin),end)
            }
          },20)
      }
    },
    mounted() {
      window.onmousewheel = ()=> {
        clearInterval(this.timer);
        this.timer = null
      }
    },
  }
</script>

